<template>
    <div class="main">
        <Back :imgUrl="backImg" style="position:absolute"></Back>
        <div class="section">
            <div class="title"></div>
            <mt-field label="" placeholder="" v-model="username"></mt-field>
            <mt-field label="" placeholder="" type="tel" v-model="phone"></mt-field>
            <div class="label"></div>
            <address-picker :opts="obj" v-model="address"></address-picker>
            <textarea name="address_detail" v-model="address_detail" placeholder="" class="address_detail"></textarea>
            <mt-checklist v-model="defaultAddress" :options="['']"></mt-checklist>
        </div>
        <div class="save" @click="saveAddress"></div>
    </div>
</template>
<script>
import Vue from 'vue'
import addressPicker from 'vue-address-picker'
import Back from '../product/back.vue'
import { Toast } from 'mint-ui'
Vue.use(addressPicker);
export default {
    name: 'app',
    components: {
        Back
    },
    data: () => {
        return {
            selectedAddress: '',
            username: "",
            phone: "",
            address_detail: "",
            backImg: "/img/back_1.png",
            defaultAddress: [],
            setDefault: false,
            address: "",
            obj: { label: { province: '', city: '', district: '' }, default: { province: '', city: '', district: '' }, noLabel: true }
        }
    },
    methods: {
        selectAddress: function(val) {
            this.selectedAddress = val;
        },
        saveAddress: function() {
            if (!this.username) {
                Toast("")
            } else if (!this.phone) {
                Toast("")
            } else if (this.phone.length != 11) {
                Toast("")
            } else if (!this.address_detail) {
                Toast("")
            }

        }
    },

    watch: {
        defaultAddress(val) {
            var me = this;
            if (val.length > 0) {
                me.setDefault = true;
            } else {
                me.setDefault = false;
            }
        }
    }
}
</script>
<style lang="scss">
@import '../style.scss';
* {
    margin: 0;
    padding: 0;
}

.main {
    width: 100%;
    max-width: 640px;
    margin: 0 auto;
}

.section {
    padding-top: 4%;
    width: 90%;
    margin: 0 auto;
    .title {
        width: 100%;
        height: 35px;
        line-height: 35px;
        text-align: center;
        font-size: 1.2rem;
        color: #9400D3;
        margin-bottom: 2%;
        background-color: #fff;
    }
}

.label {
    padding-left: 10px;
    color: #999;
    margin: 15px 0;
}

.mint-cell-title {
    color: #999;
}

.mint-cell-value {
    position: relative;
    color: #666;
    &:after {
        content: "111"
    }
    input {
        color: #666;
    }
}

.mint-cell-value:after {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #939;
    transition: all 0.3s;
}

.mint-cell-value:hover:after {
    width: 100%;
}

.address-picker {
    width: 90%;
    margin-left: 3%;
    display: flex;
    justify-content: space-around;
    label {
        width: 28%;
        select {
            height: 32px;
            border: none;
            color: #666;
            border-bottom: 1px solid #939;
        }
        &:last-of-type{
            width: 44%;
            select{
                width: 55%;
            }
        }
    }
}

.address_detail {
    width: 95%;
    display: block;
    margin: 0 auto;
    margin-top: 3%;
    color: #666;
    border: none;
    background-color: #fcfcfc;
    box-sizing: border-box;
    padding: 5px 10px;
    height: 100px;
    font-size: 1.1rem;
}

.save {
    width: 100%;
    max-width: 640px;
    height: 40px;
    position: fixed;
    bottom: 0;
    z-index: 10; //left: 0;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #939;
    color: #fff;
}
</style>


